"use client";

import React, { useState } from "react";
import { useDeleteJob, useJobs } from "@/app/customHooks/useJobs";
import { useUserApplications } from "@/app/customHooks/useJobApplication";
import { JobApplication } from "@/app/utility/props";
import JobForm from "./JobForm";
import ApplicationTable from "./ApplicationTable";

export default function JobsList() {
  const { data: jobs, isLoading, error } = useJobs();
  const { data: applications } = useUserApplications();
  const { mutate: deleteJob } = useDeleteJob();

  const [expandedJobId, setExpandedJobId] = useState<number | null>(null);
  const [showForm, setShowForm] = useState(false);

  if (isLoading) return <p>Loading jobs...</p>;
  if (error) return <p>Failed to load jobs.</p>;

  const jobApplications = (jobId: number) =>
    applications?.filter((app: JobApplication) => app.job.id === jobId);

  return (
    <div className="bg-white rounded-lg shadow p-6">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-xl font-semibold">Jobs List</h2>
        <button
          onClick={() => setShowForm(true)}
          className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700"
        >
          Add Job
        </button>
      </div>

      {showForm && <JobForm onClose={() => setShowForm(false)} />}

      <table className="w-full text-left border-collapse">
        <thead>
          <tr>
            <th className="border-b p-2">Title</th>
            <th className="border-b p-2">Company</th>
            <th className="border-b p-2">Location</th>
            <th className="border-b p-2">Description</th>
            <th className="border-b p-2">Actions</th>
          </tr>
        </thead>
        <tbody>
          {jobs?.map((job) => (
            <React.Fragment key={job.id}>
              <tr
                className="hover:bg-gray-50 cursor-pointer"
                onClick={() =>
                  setExpandedJobId(expandedJobId === job.id ? null : job.id)
                }
              >
                <td className="p-2">{job.title}</td>
                <td className="p-2">{job.company}</td>
                <td className="p-2">{job.location}</td>
                <td className="p-2">{job.description}</td>
                <td className="p-2 flex gap-3">
                  <button
                    onClick={(e) => {
                      e.stopPropagation();
                      deleteJob(job.id);
                    }}
                    className="text-red-500 hover:text-red-700 font-medium"
                  >
                    Delete
                  </button>
                </td>
              </tr>

              {expandedJobId === job.id && (
                <tr>
                  <td colSpan={5} className="p-4 bg-gray-50">
                    <ApplicationTable
                      title={job.title}
                      applications={jobApplications(job.id) || []}
                    />
                  </td>
                </tr>
              )}
            </React.Fragment>
          ))}
        </tbody>
      </table>
    </div>
  );
}
