"use client";

import React from "react";
import { JobApplication } from "@/app/utility/props";
import { useUpdateApplicationStatus } from "@/app/customHooks/useJobApplication";

interface ApplicationTableProps {
  title: string;
  applications: JobApplication[];
}

export default function ApplicationTable({
  title,
  applications,
}: ApplicationTableProps) {
  const { mutate: updateStatus } = useUpdateApplicationStatus();

  const handleStatusChange = (
    appId: string,
    status: "applied" | "1st_round" | "2nd_round" | "selected" | "rejected"
  ) => {
    updateStatus({ id: appId, status, feedback: "" });
  };

  return (
    <div>
      <h3 className="font-semibold mb-2">Applications for {title}</h3>
      {applications.length ? (
        <table className="w-full text-left border-collapse text-sm">
          <thead>
            <tr>
              <th className="border-b p-2">Applicant</th>
              <th className="border-b p-2">Email</th>
              <th className="border-b p-2">Status</th>
              <th className="border-b p-2">Change Status</th>
            </tr>
          </thead>
          <tbody>
            {applications.map((app) => (
              <tr key={app._id}>
                <td className="p-2">
                  {app.user.firstName} {app.user.lastName}
                </td>
                <td className="p-2">{app.user.email}</td>
                <td className="p-2 capitalize">{app.status}</td>
                <td className="p-2">
                  <select
                    value={app.status}
                    onChange={(e) =>
                      handleStatusChange(
                        app._id,
                        e.target.value as
                          | "applied"
                          | "1st_round"
                          | "2nd_round"
                          | "selected"
                          | "rejected"
                      )
                    }
                    className="border rounded-md px-2 py-1"
                  >
                    <option value="applied">Applied</option>
                    <option value="1st_round">1st Round</option>
                    <option value="2nd_round">2nd Round</option>
                    <option value="selected">Selected</option>
                    <option value="rejected">Rejected</option>
                  </select>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      ) : (
        <p className="text-gray-500 text-sm">No applications yet.</p>
      )}
    </div>
  );
}
